import Toast from '../../common/toast.js'
export default {
	data() {
		return {
			Color: '#333',
			tel: '',
			code: '',
			pass: '',
			btnState: false,
			rTime: '获取验证码'
		}
	},
	methods: {
		getValidate() {
			if (this.tel == '') {
				Toast.showToast('手机号不能为空')
				return
			} else if (!(/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/.test(this.tel))) {
				Toast.showToast('手机号格式不正确')
				return
			}
		
			let self = this
			this.rTime = 60
			let rTime = this.rTime
			this.btnState = true
			let interval = setInterval(() => {
				if (--self.rTime <= 0) {
					self.rTime = '获取验证码'
					clearInterval(interval)
					self.btnState = false
				}
			}, 1000)
		},
		sure() {
			if (this.tel == '') {
				Toast.showToast('手机号不能为空')
				return
			} else if (!(/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/.test(this.tel))) {
				Toast.showToast('手机号格式不正确')
				return
			}
			
			if(this.code == '') {
				Toast.showToast('验证码不能为空')
				return
			}
			
			if(this.pass == '') {
				Toast.showToast('密码不能为空')
				return
			}
			
			if(this.pass.length < 6) {
				Toast.showToast('密码长度不能低于6位')
				return
			}
		}
	}
}
